.contact {
  padding: 1.5rem;
  background-color: #f4f4f4;
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contact h2 {
  font-size: 2.2rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.contact form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.contact form:hover {
  transform: scale(1.01);
}

.contact label {
  font-size: 1.1rem;
  color: #555;
  margin-top: 1.2rem;
  text-align: left;
}

.contact input, 
.contact textarea {
  padding: 0.8rem;
  margin-top: 0.4rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.contact input:focus,
.contact textarea:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 10px rgba(52, 152, 219, 0.1);
}

.contact textarea {
  min-height: 120px;
  resize: vertical;
}

.contact button {
  margin-top: 1.8rem;
  padding: 0.9rem 2rem;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1.1rem;
  transition: all 0.3s ease;
}

.contact button:hover {
  background-color: #3498db;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(52, 152, 219, 0.2);
}

/* Responsive Design */
@media screen and (max-width: 1200px) {
  .contact form {
    max-width: 550px;
    padding: 1.8rem;
  }
}

@media screen and (max-width: 992px) {
  .contact {
    padding: 1.5rem;
  }

  .contact form {
    max-width: 500px;
    padding: 1.5rem;
  }

  .contact h2 {
    font-size: 2rem;
  }

  .contact label {
    font-size: 1rem;
  }

  .contact input, 
  .contact textarea {
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 576px) {
  .contact {
    padding: 1rem;
  }

  .contact h2 {
    font-size: 1.8rem;
  }

  .contact form {
    padding: 1.2rem;
  }

  .contact label {
    font-size: 0.95rem;
  }

  .contact input, 
  .contact textarea {
    padding: 0.7rem;
    font-size: 0.9rem;
  }

  .contact button {
    padding: 0.8rem 1.8rem;
    font-size: 0.95rem;
  }
}