.home {
    min-height: 100vh;
    background-color: #f4f4f4;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
}

.content-container {
    width: 100%;
    max-width: 1400px;
    display: grid;
    grid-template-columns: 1.1fr 1fr;
    gap: 2rem;
    padding: 2rem;
    margin: 0 auto;
}

.text-section {
    display: flex;
    flex-direction: column;
    gap: 2.5rem;
    justify-content: center;
}

h1 {
    font-size: 3.2rem;
    color: #333;
    margin: 0;
    line-height: 1.2;
}

.running-lines {
    font-size: 1.3rem;
    color: #555;
    margin: 0;
    line-height: 1.7;
    max-width: 600px;
}

.name {
    color: #3498db;
    font-weight: bold;
}

.social-icons {
    display: flex;
    gap: 4rem;
    margin-top: 1rem;
}

.icon-circle {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    background-color: #f0f0f0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333;
    text-decoration: none;
    transition: all 0.3s ease;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.icon-circle:hover {
    background-color: #3498db;
    color: white;
    transform: translateY(-2px);
}

.image-section {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 2rem;
}

.profile-image {
    width: 550px;
    height: 550px;
    border-radius: 50%;
    object-fit: cover;
    border: 5px solid #333;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

/* Responsive Design */
@media screen and (max-width: 1200px) {
    .content-container {
        max-width: 1200px;
        gap: 2rem;
    }

    .text-section {
        padding-left: 2rem;
    }

    .profile-image {
        width: 450px;
        height: 450px;
    }

    h1 {
        font-size: 2.8rem;
    }

    .running-lines {
        font-size: 1.2rem;
    }
}

@media screen and (max-width: 992px) {
    .content-container {
        grid-template-columns: 1fr;
        text-align: center;
        gap: 3rem;
        padding: 1rem;
    }

    .text-section {
        align-items: center;
        padding-left: 0;
        gap: 2rem;
    }

    .running-lines {
        margin: 0 auto;
    }

    .image-section {
        justify-content: center;
        padding-right: 0;
    }

    .profile-image {
        width: 400px;
        height: 400px;
    }
}

@media screen and (max-width: 576px) {
    .home {
        padding: 1rem;
    }

    h1 {
        font-size: 2.2rem;
    }

    .running-lines {
        font-size: 1.1rem;
        max-width: 100%;
        padding: 0 1rem;
    }

    .profile-image {
        width: 300px;
        height: 300px;
    }

    .icon-circle {
        width: 38px;
        height: 38px;
    }

    .social-icons {
        gap: 2.5rem;
    }
}
